.button {
  font-family: inherit;

  @include box(280px, 40px);
  background-color: $green;
  color: $alt-base-color;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 14px;
  padding-bottom: 14px;
  border: none;
  text-transform: uppercase;
  font-size: $secondary-fs;
  line-height: $secondary-lh;
  cursor: pointer;

  &:hover {
    background: $bg-green;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
