.explanation {
  font-size: $body-fs;
  line-height: $body-lh;
  padding-bottom: 20px;
  counter-reset: product;
}

.explanation__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  padding-right: 6px;
}

.explanation__title {
  font-size: $title-h1-fs;
  line-height: $title-h1-lh;
  padding-bottom: 26px;
}

.explanation__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}

.explanation__list-item:first-of-type {
  padding-top: 3px;
}

.explanation-list__text {
  display: flex;
  flex-direction: row;
  width: 100%;

  &::before {
    content: "";
    display: block;

    @include box(60px, 60px);
    background-color: $green;
    background-position: center;
    background-repeat: no-repeat;
    flex-shrink: 0;
    margin-right: 18px;
    margin-bottom: 20px;
  }
}

.explanation-list__text--leaf {
  &::before {
    background-image: url("../img/advantage-leaf@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-leaf@2x.png");
      background-size: 52px 48px;
    }
  }
}

.explanation-list__text--powder {
  &::before {
    background-image: url("../img/advantage-powder@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-powder@2x.png");
      background-size: 58px 62px;
    }
  }
}

.explanation-list__text--cultery {
  &::before {
    background-image: url("../img/advantage-dining@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-dining@2x.png");
      background-size: 46px 60px;
    }
  }
}

.explanation-list__text--clock {
  &::before {
    background-image: url("../img/advantage-clock@1x.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantage-clock@2x.png");
      background-size: 49px 56px;
    }
  }
}

.explanation__br {
  display: none;
}

@media (min-width: $tablet-width) {
  .explanation {
    padding-bottom: 7px;
  }

  .explanation__inner {
    padding-top: 16px;
    padding-bottom: 0;
  }

  .explanation__list {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .explanation__list-item:first-of-type {
    padding-top: 0;
  }

  .explanation-list__item {
    width: 50%;
    display: flex;
    margin-bottom: 133px;
    padding-right: 146px;
    position: relative;

    &:nth-child(odd) {
      .explanation-list__text::after {
        top: 100px;
        left: 120px;
      }
    }

    &:nth-child(2n) {
      margin-left: auto;
      padding: 0;
      padding-left: 35px;
      padding-right: 48px;

      .explanation-list__text::after {
        left: 145px;
        top: 100px;
      }
    }
  }

  .explanation__br {
    display: block;
  }

  .explanation__title {
    font-size: $big-title-fs;
    line-height: $title-h1-lh;
    padding-bottom: 74px;
  }

  .explanation-list__text {
    flex-direction: column;
    font-size: $secondary-fs;
    line-height: 24px;

    &::before {
      @include box(80px,80px);
      margin-bottom: 33px;
    }

    &::after {
      display: block;
      font-size: 281px;
      line-height: 1px;
      color: $base-color;
      opacity: 0.1;
      position: absolute;
      top: 55%;
      left: 57%;
      font-family: $main-font;
    }
  }

  .explanation-list__text--leaf::after {
    counter-increment: product;
    content: counter(product);
  }

  .explanation-list__text--powder::after {
    counter-increment: product;
    content: counter(product);
  }


  .explanation-list__text--cultery::after {
    counter-increment: product;
    content: counter(product);
  }

  .explanation-list__text--clock::after {
    counter-increment: product;
    content: counter(product);
  }
}

@media (min-width: $desktop-width) {
  .explanation {
    padding-bottom: 0;
  }

  .explanation__inner {
    padding: 0;
    padding-top: 40px;
  }

  .explanation__list {
    justify-content: space-between;
  }

  .explanation__title {
    padding-bottom: 73px;
    padding-top: 3px;
  }

  .explanation-list__item {
    width: 25%;
    margin-right: 0;
    margin-bottom: 0;
    padding-right: 0;
    min-height: 233px;
  }


  .explanation-list__item:nth-child(even) {
    margin-left: 0;
    margin-right: 0;
    padding-right: 0;
    padding-left: 19px;

    .explanation-list__text::after {
      left: 130px;
      top: 100px;
    }
  }

  .explanation-list__item:nth-child(odd) {
    padding-left: 40px;

    .explanation-list__text {
      &::after {
        left: 153px;
        top: 100px;
      }
    }
  }

  .explanation-list__item:nth-child(4n) {
    .explanation-list__text::after {
      left: 173px;
    }
  }

  .explanation-list__item:first-of-type {
    padding-left: 0;

    .explanation-list__text {
      &::after {
        left: 120px;
      }
    }
  }

  .explanation-list__item:last-of-type {
    padding-left: 16px;
    justify-content: flex-end;

    .explanation-list__text {
      width: 85%;
    }
  }

  .explanation-list__text {
    width: 80%;

    &::before {
      width: 80px;
      height: 80px;
      padding-top: 40px;
    }

    &::after {
      top: 54%;
      left: 48%;
    }
  }
}
